import React from 'react';
import Container from '../components/container';
import Text from '../components/text';

const dummyPhoto = [
  {
    Img:
      'https://images.pexels.com/photos/1363876/pexels-photo-1363876.jpeg?cs=srgb&dl=calm-body-of-water-1363876.jpg&fm=jpg',
  },
  {
    Img:
      'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcRvQRjwx3Ro7QqN2F2E_2EMHjhdXYyp0RIodGVoj9JyB2nGgQBV&usqp=CAU',
  },
  {
    Img:
      'https://www.apple.com/newsroom/images/product/iphone/lifestyle/Apple_Shot-on-iPhone-Challenge-2020_Austin-Mann_01072020_big.jpg.large.jpg',
  },
];

const style = {
  width: '300px',
  height: '200px',
};

const Home = () => {
  return (
    <Container flex_direction='column'>
      <Text fontSize='mainTitle'>하루 그림</Text>
      <Text>당신의 그림습관</Text>
      <Text style={{ margin: '100px' }} fontSize='massive'>
        "스케치북"
      </Text>
      <Text>오늘의 그림들</Text>
      <Container>
        {dummyPhoto.map((item) => (
          <div style={{margin:"", width:"400px", height:"250px", background: `url("${item.Img}")` }}></div>
        ))}
      </Container>
    </Container>
  );
};

export default Home;
